.footer {
    background-color: #2525EB;
    min-height: 258px;
    padding: 50px;

    .footer-container {
        margin: 0 auto;
    }
    &__items-wrp {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 20px;
        @media (max-width: 530px) {
            flex-direction: column;
            margin: 0 auto;
            width: max-content;
        }
    }
    &__item {
    }
    &__title {
        font-size: 30px;
        color: #fff;
        font-weight: 700;
        @include adaptiveAllCont('font-size', 30, 20, 1400, 320);
        margin-bottom: 25px;
    }
    li {
        font-size: 15px;
        font-weight: 700;
        color: #fff;
        cursor: pointer;
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }

}
