
.brands {
    min-height: 485px;

    .brands-container {
        margin: 0 auto;
    }
    .brands-title {
        color: #000;
        margin-bottom: 30px;
        @media (max-width: 585px) {
            text-align: center;
        }
    }
    .brands__wrp {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        gap: 20px 10px;
        flex-wrap: wrap;
        @media (max-width: 585px) {
            flex-direction: column;
            justify-content: center;
        }

        &::after {
            content: '';
            width: 402px;
            height: 3px;
            background-color: #969696;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: -105px;
            @media (max-width: 585px) {
                bottom: -29px;
            }
            @media (max-width: 416px) {
                width: 95%;
            }
        }
    }
    .brands__item {
        display: block;
        max-width: 180px;
        @media (max-width: 585px) {
            max-height: 60px;
        }
    }
    .brands__social-wrp {
        display: flex;
        margin: 0 auto;
        column-gap: 50px;
        width: max-content;
        margin-top: 188px;
        @media (max-width: 585px) {
            margin-top: 70px;
        }
    }
    .brands__social-item {
        display: block;
        align-items: center;
        flex: 0 0 32px;
        cursor: pointer;
    }

}

