.main2 {
    margin-top: 30px;
    position: relative;
    .middle-middle {
        background-color: #FF5314;
    }

    &__title {
        font-size: 45px;
        font-weight: 700;
        color: #fff;
    }
    &__cards-wrp {
        margin-top: 27px;
        display: flex;
        width: 100%;
        justify-content: space-between;
        @media (max-width: 1030px) {
            display: none;
        }

    }
    &__card-item {
        width: 233px;
        height: 307px;
        background-color: #fff;
        padding: 0 10px;
        position: relative;
        @include adaptiveAllCont('width', 233, 190, 1260, 1030);
        @media (max-width: 1030px) {
            width: 100%;
        }
        @media (max-width: 340px) {
            width: 215px;
            margin: 0 auto;
        }
    }
    &__discount-img {
        position: absolute;
        top: 0;
        left: 0;
    }
    &__phone-img {
        display: block;
        margin: 0 auto;
        margin-top: 40px;
    }
    &__card-bottom-wrp {
        margin-top: 20px;
        padding-left: 10px;
        display: flex;
    }
    &__card-bottom-text {

    }
    &__phone-title {
        font-size: 13px;
        font-weight: 700;
        text-transform: uppercase;
    }
    &__price-wrp {
        width: 100%;
        margin-top: 10px;
    }
    &__current-price {
        display: block;
        font-weight: 700;
        font-size: 18px;
    }
    &__prev-price {
        display: block;
        text-decoration: line-through;
        margin-top: 5px;
        color: #818181;
    }
    &__card-cart-img {
        display: block;
        width: 32px;
        height: 32px;
        margin-top: 35px;
        cursor: pointer;
    }
}

.swiper {
    width: 800px;
    height: 307px;
    display: none;
    @media (max-width: 1030px) {
        display: block;
    }
    @media (max-width: 868px) {
        width: 100%;
    }
  }
  .swiper-choice {
    height: 335px;
  }
  .choice {
    .choice-title {
        color: #000;
        margin-bottom: 30px;
    }
  }
  .choice-card-item {
    border: 10px solid #FF8253;
  }

