
.main {
    // flex: 1 1 auto;
    min-height: 475px;
    background-color: #2525EB;

    &__content {
        display: flex;
    }
    &__left {
        width: 863px;
        width: calc(863 / 1400 * 100%);
    }
    .main-left {
    }
    &__left-top {
        display: flex;
    }

    &__left-top,
    &__middle-top,
    &__right-top {
        height: 303px;
        @media (max-width: 768px) {
            height: 242px;
        }
    }
    &__left-middle {
        background-color: #FF5314;
        height: 83px;
        @media (max-width: 768px) {
            height: 30px;
        }
    }
    &__left-bottom {
        display: flex;
        align-items: center;
        justify-content: center;

        @media (max-width: 768px) {
            justify-content: flex-start;
        }
        ul {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 10px 10px;
            @media (max-width: 1050px) {
                justify-content: space-between;
                text-align: center;
                width: 100%;
            }
            @media (max-width: 768px) {
                display: none;
            }
        }
        li {
            font-size: 20px;
            @include adaptiveAllCont('font-size', 20, 12, 1400, 768);
            color: #fff;
            padding: 10px 40px;
            border-left: 1px solid #fff;
            cursor: pointer;
            transition: background .3s;
            display: flex;
            @include adaptiveAllCont('padding-left', 40, 20, 1400, 768);
            @include adaptiveAllCont('padding-right', 40, 20, 1400, 768);
            &:last-child {
                border-right: 1px solid #fff;
            }
            &:hover {
                background: rgba(255, 255, 255, 0.4);
            }
            @media (max-width: 1050px) {
                width: 100%;
                text-align: center;
                justify-content: center;
            }
        }
    }


    &__left-left {
        width: 325px;
        border-right: 1px solid #fff;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: relative;

        @media (max-width: 768px) {
            //top: 30px;
            left: -71px;
            border: none;
            position: static;
            padding: 0 10px;
        }
        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 4px;
            height: 4px;
            background-color: #FF5314;
            z-index: 1;
            @media (max-width: 768px) {
                display: none;
            }
        }

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            right: -1px;
            width: 4px;
            height: 4px;
            background-color: #FF5314;
            z-index: 1;
            @media (max-width: 768px) {
                display: none;
            }
        }
    }
    &__left-right {
        position: relative;
        width: calc(573/896 * 100%);
        img {
           position: absolute;
           top: -82px;
           right: -24px;
           pointer-events: none;
           @include adaptiveAllCont('width', 452, 186, 1400, 320);
           @media (max-width: 1258px) {
                top: -58px;
           }
           @media (max-width: 1000px) {
                top: -20px;
            }
            @media (max-width: 485px) {
                transform: translate(39px, 42px);
            }
        }
        span {
            font-size: 44px;
            font-weight: 700;
            color: #fff;
            position: absolute;
            bottom: 0;
            left: 0;
            @include adaptiveAllCont('font-size', 44, 23, 1400, 320);
        }
    }
    &__left-title-wrp {
        display: flex;
        align-items: center;
        flex: 1 1 auto;
        justify-content: center;
        @media (max-width: 768px) {
            display: block;
            transform: translateY(10px);
        }
    }
    &__left-title {
        font-size: 38px;
        font-weight: 700;
        color: #fff;
        @include adaptiveAllCont('font-size', 38, 20, 1400, 320);
        text-transform: uppercase;
        flex: 0 0 auto;
    }
    &__playst {
        border: 4px solid #fff;
        border-right: 3px solid #fff;
        flex: 0 0 140px;
        position: relative;
        @media (max-width: 768px) {
            border: none;
        }
        &::before {
            content: '';
            position: absolute;
            top: -4px;
            left: -4px;
            width: 4px;
            height: 4px;
            background-color: #FF5314;
            z-index: 1;
            @media (max-width: 768px) {
                display: none;
            }
        }

        &::after {
            content: '';
            position: absolute;
            top: -4px;
            right: -4px;
            width: 4px;
            height: 4px;
            background-color: #FF5314;
            z-index: 1;
            @media (max-width: 768px) {
                display: none;
            }
        }
        img {
            position: absolute;
            bottom: -0.5px;
            left: 0;
            @media (max-width: 768px) {
                position: static;
                margin-top: 93px;
            }
            @media (max-width: 440px) {
                display: none;
            }
        }
    }
    &__playst-text {
        font-size: 16px;
        color: #fff;
        margin-top: 12px;
        margin-left: 12px;
        @media (max-width: 768px) {
            transform: translateY(10px);
        }
    }


//middle====================
    &__middle {
        width: calc(376 / 1400 * 100%);
    }

    &__middle,
    &__middle-middle,
    &__right-middle{
        height: 83px;
    }
    .main-middle {
    }
    &__middle-top {
        background-color: #FF5314;
        padding: 12px;
        @media (max-width: 505px) {
            padding: 5px;
        }
        p {
            font-size: 68px;
            font-weight: 700;
            color: #fff;
            margin-top: 69px;
            @include adaptiveAllCont('font-size', 68, 18, 1400, 320);
        }
    }
    &__middle-middle {
        display: flex;
        align-items: center;
        justify-content: center;
        @media (max-width: 768px) {
            height: 30px;
        }
        img {
            @media (max-width: 1236px) {
                max-width: 96%;
            }
        }
    }
    &__middle-bottom {
        background-color: #FF5314;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5px;
        @media (max-width: 768px) {
            min-height: 203px;
        }
        p {
            font-size: 25px;
            color: #fff;
            @include adaptiveAllCont('font-size', 25, 10, 1400, 320);
        }
    }


    .main__right {
        width: calc(161 / 1400 * 100%);
    }
    .main-right {
    }
    &__right-top {
    }
    &__right-middle {
        background-color: #FF5314;
        @media (max-width: 768px) {
            height: 30px;
        }
    }
    &__right-bottom,
    &__middle-bottom,
    &__left-bottom {
        height: 88px;
        @media (max-width: 768px) {
            height: auto;
        }
    }

}

.playst-price {
    position: absolute;
    bottom: 0;
    right: 0;
    padding-right: 5px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    span {
        display: flex;
        color: #fff;
        margin-bottom: 5px;
    }
    &__before {
        text-decoration: line-through;
        margin-left: auto;
        font-size: 12px;
        font-weight: 700;
    }
    &__current {
        width: 125px;
        height: 30px;
        background-color: #FF5314;
        font-size: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 2px;
    }
    &__shop {
        font-size: 8px;
    }
}
