
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap');

@import "_mixins";
@import "_null";
@import "_header";
@import "_menu";
@import "_main";
@import "_main2";
@import "_swiper";
@import "_daysGoods";
@import "_brands";

@import "_footer";

.wrp {
    overflow: hidden;
    background-color: #fff;
    color: #222;
    display: flex;
    flex-direction: column;
    min-height: 100%;
}
.container {
    max-width: 1430px;
    padding: 0 15px;
    margin: 0 auto;
}
.container2 {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    width: 1340px;
    padding: 30px 15px;
    @media (max-width: 1335px) {
        width: 100%;
    }
}
.container3 {
    width: 1340px;
    padding: 30px 15px;
    @media (max-width: 1335px) {
        width: 100%;
    }
}
.main2__title {
    @include adaptiveAllCont('font-size', 45, 24, 1400, 320)
}
.swiper-scrollbar-drag {
    background-color: #ff5314;
}





