
.days-goods {
    width: 100%;
    min-height: 348px;
    background-image: url(../img/goods_day/goods_day_bg.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-top: 34px;

    &__content {
        display: flex;
        @media (max-width: 1115px) {
            flex-direction: column-reverse;
            align-items: center;
        }
        span {
            display: block;
        }
    }
    &__date-wrp {
        padding: 65px 0px 0px 50px;
        @media (max-width: 1115px) {
            padding: 0;
            margin-top: 30px;
        }
    }
    &_title {
        font-size: 26px;
    }
    &__date {
        font-size: 51px;
        font-weight: 900;
        margin-top: 5px;
        display: flex;
        @include adaptiveAllCont('font-size', 37, 28, 1300, 320);
    }
    &__limit {
        margin-top: 10px;
        font-size: 15px;
    }
    &__counter {
        width: 180px;
        height: 72px;
        background-color: #FFF9B0;
        padding: 10px 7px;
        margin-top: 15px;
    }

    .end-action {
        text-transform: uppercase;
        font-size: 13px;
        text-align: center;
    }
    .count-numbers {
        line-height: 1.15;
        font-size: 37px;
        font-weight: 700;
        text-align: center;
        margin-top: 4px;
        @include adaptiveAllCont('font-size', 37, 28, 1300, 320);
    }
    &__washer-wrp {
        width: 808px;
        min-height: 300px;
        background-color: #fff;
        margin-left: 70px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 30px 60px;
        @media (max-width: 1115px) {
            margin-left: 0;
        }
        @media (max-width: 830px) {
            width: 100%;
            padding: 30px 15px;
        }
        @media (max-width: 628px) {
            flex-direction: column;
            row-gap: 20px;
        }
        span {
            display: block;
        }
    }
    &__washer-left {
        img {
            width: 216px;
        }
    }
    &__washer-title {
        font-size: 37px;
        font-weight: 700;
        @include adaptiveAllCont('font-size', 37, 28, 1300, 320);
    }
    &__washer-discount {
        margin-top: 26px;
        display: flex;
        align-items: center;
    }
    &__washer-price-before {
        font-size: 25px;
        font-weight: 700;
        text-decoration: line-through;
        margin-right: 8px;
    }
    &__washer-discount-sum {
        background-color: #FFF9B0;
        border-radius: 4px;
        line-height: 1.15;
        padding: 6px 4px 4px 5px;
        font-size: 12px;
        font-weight: 700;
    }
    &__washer-price-now {
        width: 216px;
        height: 51px;
        background-color: #FF5314;
        color: #fff;
        padding: 7px 15px;
        margin-top: 27px;
    }
    &__valute {
        margin-left: auto;
        display: block;
        width: max-content;
    }
    &__washer-sum-now {
        font-size: 26px;
        font-weight: 700;
        .ot {
            font-size: 20px;
            display: inline;
            margin-right: 8px;
        }
    }
    &__washer-right {
        img {
            @media (max-width: 380px) {
                width: 100%;
            }
        }
    }
}

//timer=======================================
.timer__items {
    display: flex;
    justify-content: center;
  }
  .timer__item {
    position: relative;
    margin-left: 4px;
    margin-right: 4px;
    padding-bottom: 15px;
    text-align: center;
  }
  .timer__item::before {
    content: attr(data-title);
    display: block;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    font-size: 14px;
  }
  .timer__item:not(:last-child)::after {
    content: ':';
    position: absolute;
    right: -9px;
    bottom: 17px;
  }

  //banner===================================================
  .banner {
    width: 100%;
    height: 90px;
    background-image: url(../img/goods_day/banner.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;

    &__content {
        display: flex;
        align-items: center;
    }
    &__stick {
        display: block;
        margin-left: 12px;
        margin-right: 16px;
    }
    &__text {
        text-transform: uppercase;
        color: #fff;
        font-size: 17px;
        font-weight: 700;
    }
  }

  //new======================================================
  .new {
    width: 100%;
    min-height: 609px;
    .new-container {
        margin: 0 auto;
    }
    .new-title {
        color: #000;
    }
  }
  .new-cards-wrp {
    border: 5px solid #2525EB;
    height: 400px;
    margin-top: 30px;
  }
  .new-btn {
    width: 236px;
    height: 65px;
    border-radius: 15px;
    background-color: #FF5314;
    color: #fff;
    text-transform: uppercase;
    margin: 0 auto;
    display: block;
    margin-top: 27px;
    cursor: pointer;
    transition: background .3s;
    &:hover {
        background-color: #c64414;
    }
  }
  .new-card-item {
    height: 100%;
    width: 20%;
    justify-content: start;
    &:not(:last-child) {
        border-right: 5px solid rgba(37, 37, 235, 0.4);
    }
    .new-phone-img {
        margin: 0 auto;
        margin-top: 40px;
    }
  }

  //laptops===========================
.laptops {
    min-height: 580px;
    width: 100%;
    padding: 20px 0;

    .laptops-container {
        margin: 0 auto;
    }
    .laptops-title {
        color: #000;
    }
    .laptops-item {
        border: 1px solid #2525EB;
        position: relative;
    }
    .laptops-lap {
        transform: translateY(20px);
    }
    .acer-img {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 53px;
    }
    .laptop-swiper {
        height: 360px;
        margin-top: 30px;
    }
}





