
.burger {
    position: absolute;
    top: 33px;
    right: 15px;
    z-index: 4;
    width: 30px;
    height: 30px;
    display: none;
    cursor: pointer;
    @media (max-width: 990px) {
        display: block;
    }
    @media (max-width: 465px) {
        top: 26px;
        right: 2px;
    }
    span {
        width: 30px;
        height: 2px;
        background-color: #000;
        position: absolute;
        pointer-events: none;
        transition: all .3s;
        &:nth-child(1) {
            &.active {
                transform: rotate(-45deg);
                top: 7px;
            }
        }
        &:nth-child(2) {
            top: 7px;
            &.active {
                //display: none;
                opacity: 0;
                transform: translateX(-50px);
            }
        }
        &:nth-child(3) {
            top: 14px;
            &.active {
                top: 7px;
                transform: rotate(45deg);
            }
        }
    }
}


.menu {
    margin: 0 auto;
    display: block;
    position: relative;
    transition: transform .3s;

    @media (max-width: 990px) {
        overflow: auto;
        position: fixed;
        right: 0;
        top: 0;
        z-index: 3;
        width: 400px;
        background-color: #fff;
        padding: 20px 15px 20px 15px;
        transform: translateX(100%);
        height: 100vh;
        &.active {
            transform: translateX(0);
        }
        &:first-child {
            &.active {
                justify-content: end;
            }
        }
        @media (max-width: 400px) {
            width: 100%;
        }
    }
    // &.active {
    //     width: 100%;
    // }
    &__list {
        display: flex;
        align-items: center;
        transition: all .3s;
        flex-wrap: wrap;
        row-gap: 15px;

        @media (max-width: 990px) {
            display: block;
            min-height: 100%;
            padding: 20px 0;
        }
    }
    &__item {
       padding: 5px 28px;
       border-right: 1px solid #222;
       transition: background .5s;
       cursor: pointer;
        &:hover {
            background-color: #FFA888;
        }
        a {
            font-size: 16px;
            font-weight: 500;
            color: #222;
            @media (max-width: 990px) {
                font-size: 22px;
            }
        }
        @media (max-width: 1370px) {
            padding: 5px 15px;
        }
        @media (max-width: 990px) {
            border-right: none;
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
    }
}

body {
    &.active {
        overflow: hidden;
    }
}