.header {
    width: 100%;
    position: relative;
    padding: 15px 0;
    &__container {
        @media (max-width: 990px) {
            width: 100%;
        }
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        //padding-right: 82px;
        @media (max-width: 1370px) {
            padding-right: 0;
        }
    }
    &__logo {
        margin-right: 15px;
        img {
            width: 320px;
            @media (max-width: 1320px) {
                @include adaptiveAllCont('width', 320, 203, 1320, 320);
            }
        }
    }

    .search-dekst {
        @media (max-width: 990px) {
            display: none;
        }
    }
    .search-mob {
        display: none;
        @media (max-width: 990px) {
            display: flex;
            margin-top: 30px;
            margin-right: 0;
        }
    }

    &__search-wrp {
        flex: 0 0 626px;
        height: 45px;
        border: 1px solid #FF5314;
        display: flex;
        margin-right: 15px;
        @media (max-width: 1320px) {
            flex: 1 1 auto;
        }
    }
    &__search-btn {
        height: 100%;
        width: 190px;
        background-color: #FF5314;
        color: #fff;
        font-weight: 500;
        font-size: 22px;
        cursor: pointer;
        transition: background .3s;
        @media (max-width: 360px) {
            font-size: 18px;
        }
        &:hover {
            background-color: #e24005;
        }
        img {
            position: relative;
            transform: translate(-10px, 2px);
            @media (max-width: 350px) {
                display: none;
            }
        }
        @media (max-width: 1030px) {
            width: 135px;
        }
    }
    &__search {
        display: block;
        height: 100%;
        font-size: 16px;
        padding: 0 10px;
        flex: 1 0 auto;
    }
    &__icons {
        @media (max-width: 990px) {
            transform: translateX(-65px);
        }
        @media (max-width: 500px) {
            transform: translateX(0);
            flex: 1 1 auto;
        }
    }
    .icons-dekst {
        @media (max-width: 500px) {
            display: none;
        }
    }
    .icons-mob {
        display: none;
        @media (max-width: 500px) {
            display: block;
        }
    }
    &__for-buyers {
        display: flex;
        align-items: center;
        @media (max-width: 500px) {
            justify-content: flex-end;
        }
    }
    &__for-buyers-item {
        cursor: pointer;
        &:not(:last-child) {
            margin-right: 52px;
            @include adaptiveAllCont('margin-right', 52, 10, 1430, 320);
        }
        span {
            display: block;
            margin-top: 6px;
        }
        img {
            display: block;
            margin: 0 auto;
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        margin-top: 24px;
        @media (max-width: 500px) {
            justify-content: space-between;
        }
    }
    &__categories {
        width: 189px;
        height: 46px;
        background-color: #FF5314;
        margin-right: 18px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        padding: 11px;
        position: relative;
        cursor: pointer;
        transition: background .3s;
        position: relative;
        @media (max-width: 370px) {
            width: 136px;
        }


        &:hover {
            background-color: #e24005;
            .header__category-list {
                opacity: 1;
                visibility: visible;
            }
            &::before {
                transform: rotate(-45deg);
            }
            &::after {
                transform: rotate(45deg);
            }
        }
        &::before {
            content: '';
            width: 10px;
            height: 2px;
            background-color: #fff;
            position: absolute;
            right: 20px;
            top: 22px;
            transform: rotate(45deg);
            transition: all .3s;
            @media (max-width: 340px) {
                display: none;
            }
        }
        &::after {
            content: '';
            width: 10px;
            height: 2px;
            background-color: #fff;
            position: absolute;
            right: 14px;
            top: 22px;
            transform: rotate(-45deg);
            transition: all .3s;
            @media (max-width: 340px) {
                display: none;
            }
        }
        img {
            margin-right: 8px;
            @media (max-width: 370px) {
                display: none;
            }
        }
        span {
            color: #fff;
            font-size: 22px;
            font-weight: 500;
            @media (max-width: 1170px) {
                font-size: 18px;
            }
        }
    }
    &__category-list {
        position: absolute;
        top: 49px;
        left: 0;
        width: 338px;
        background-color: #fff;
        z-index: 2;
        filter: drop-shadow(0px 6px 10px rgba(0, 0, 0, 0.25));
        padding: 27px 15px 27px 45px;
        opacity: 0;
        visibility: hidden;
        transition: all .3s;
        cursor: auto;
    }
    &__category-item {
        font-weight: 700;
        text-transform: uppercase;
        font-size: 12px;
        cursor: pointer;
        &:not(:last-child) {
            margin-bottom: 27px;
        }
    }
}